.face-tracker-container {
  text-align: center;
  margin: 20px;
}

.controls {
  margin: 20px 0;
}

.start-btn, .stop-btn {
  padding: 5px 10px;
  border-radius: 40px;
  border: none;
  margin: 0px 10px;
  line-height: 30px;
}

.start-btn {
  background-color: #6a3dff;
  color: white;
}

.stop-btn {
  background-color: #ff005d;
  color: white;
}

.start-btn:disabled, .stop-btn:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.video-container {
  position: relative;
  display: inline-block;
  border: 5px solid #1e1e1e;
  background: #00000060;
  border-radius: 10px;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 10px;
}
